.fixed-menu {
    position: fixed;
    top: 64px;
    z-index: 999;
    border-bottom: 1px solid #e1e1e1;
    border-top-left-radius: 0;
    background-color: rgb(245,246,250);
    min-height: 61px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.fixed-menu .Menu.Item {
    white-space: nowrap;
    margin: 0 10px;
    color: #8894A1;
    font-weight: 500;
    text-decoration: none;
    outline: none;
    transition: all 0.3s;
    cursor: pointer;
    background-color: transparent;
}

.fixed-menu .select-user-share .ant-modal-wrap {
    background-color: red;
}
.custom-menu{
    margin: 0 auto !important;
    top: 50%;
    transform: translateY(-50%) !important;
    width: 600px;
}
.custom-menu .ant-select {
  margin-bottom: 15px;
}
.menu-title-alert {
    margin-bottom: 10px;
    margin-top: 20px;
    text-align: center;
}
.menu-btn-delete-checkbox{
    margin-top: '10px'; 
    margin-bottom: '10px'; 
}
.fixed-width-object{
    width: 100%;
}
