@media only screen and (max-width: 809px) {
    .menu-detail-request .fixed-menu .ant-menu-overflow-item-rest {
        opacity: 1 !important;
        order: 3 !important;
    }
  }

@media only screen and (max-width: 1200px) {
    .menu-detail-request .fixed-menu {
        display: flex;
    }  
  }


  @media only screen and (min-width: 1200px) {
    .menu-detail-request .fixed-menu {
        display: flex;
    }
    .menu-detail-request .fixed-menu .ant-menu-item {
        position: unset !important;
        overflow-y: unset !important;
        height: fit-content !important;
        opacity: 1 !important;
    }

    .menu-detail-request .fixed-menu .ant-menu-overflow-item-rest {
        position: unset !important;
        overflow-y: unset !important;
        opacity: 0 !important;
        order: 12 !important;
    }
  }
  .menu-detail-request .ant-menu-overflow{
    width: auto;
  }
  .menu-detail-request .ant-menu-overflow .ant-menu-item::after{
    border-bottom: none;
  }



  